body {
  margin: 0;
  padding: 0;

  font-family: $main-font, $type-font;
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  color: $text-color;
  background-color: $primary-color;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip: rect(0 0 0 0);
  overflow: hidden;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100%;

  &--show {
    display: block;
  }
}

.btn-orange {
  margin: 0;
  padding: 0 0 2px;

  line-height: 17px;
  color: #ff7931;

  border-color: transparent;
  border-width: 1px;
  border-bottom-color: rgba(255,121,49,.2);
  background-color: transparent;  
  cursor: pointer;

  &--big {
    font-size: 15px;
    line-height: 18px;
  }
}

@media (min-width: $desktop-width) {
  .wrapper {
    display: flex;
    width: 1170px;
    margin: 0 auto;
    padding: 0;
  }
}
